import React from "react";
import { Button, Divider, Grid, TextField, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import LoginIcon from "@mui/icons-material/Login";
import LogoDC from "../../assets/log/doctors.svg";

const SupportAsistencia = () => {
  return (
    <Grid container justifyContent="center">
      <Grid item xs={5} sm={5} mb={2} marginTop={1}>
        <img src={LogoDC} alt="logoDC"></img>
      </Grid>

      <Grid item xs={12} sx={{ mt: 0 }}>
        <Grid item xs={12} sx={{ mb: 1 }}>
          <Divider />
        </Grid>
        <Typography>
          {" "}
          Ingresa tu correo electrónico o número de documento para restablecer
          tu cuenta.{" "}
        </Typography>
      </Grid>

      <Grid item xs={12} sx={{ mt: 2 }}>
        <TextField
          label="Correo electrónico o número de documento"
          type="text"
          placeholder="Correo electrónico o número de documento"
          fullWidth
          required
        />
      </Grid>

      <Grid item xs={12} sx={{ mt: 3 }}>
          <Divider />
        </Grid>
      <Grid container justifyContent="center" spacing={2} sx={{ mb: 1, mt: 1 }}>
        <Grid item xs={12} sm={6}>
          <Button
            variant="contained"
            fullWidth
            sx={{ mb: 1, mt: 0, borderRadius: 2 }}
            size="large"
          >
            Enviar
          </Button>
        </Grid>
      </Grid>

      <Grid container direction="row" justifyContent="left">
        <Link
          to="/loginasistencial"
          style={{ color: "#9C239C" }}
          activeStyle={{ color: "#E3F2FD" }}
        >
          <Button
            startIcon={<LoginIcon titleAccess="inicio Sesión"></LoginIcon>}
          >
            <Link
              to="/loginasistencial"
              style={{ color: "#9C239C" }}
              activeStyle={{ color: "#E3F2FD" }}
            >
              Iniciar sesión
            </Link>
          </Button>
        </Link>
      </Grid>
    </Grid>
  );
};

export default SupportAsistencia;
